#header {
  background-image: url(../images/header/header-bg.jpg);
  background-repeat: no-repeat;
  background-position: center;
  //   background-size: cover;
  background-color: #4690ff;

  min-height: 2600px;

  text-align: center;
}

.header__nav {
  padding-top: 40px;
  padding-bottom: 40px;
}

.header__top {
  margin: 0 auto;
  max-width: 812px;
}

.header__wrapper {
  padding-top: 50px;
}

.header__link {
  margin: 0 auto;
  margin-top: 40px;
  
  display: flex;
  flex-direction: column;
  align-items: center;

  max-width: 640px;

  & img {
    margin: 0 auto;
  }
}

.header__link a > * + * {
  margin-top: 40px;
}

.header__link > * + * {
  margin-top: 40px;
}
