/* Reset and base styles  */
*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  height: 100%; /* sticky footer */
}

body {
  height: 100%; /* sticky footer */
  display: flex; /* sticky footer */
  flex-direction: column; /* sticky footer */
  margin: 0 !important;
  font-family: generalFont, sans-serif !important;
  font-size: 32px;
  font-weight: 600;

  color: white;
  background-color: $bg-color !important;
  scroll-behavior: smooth;
}

body p {
  line-height: 1.3em;
}

body.noscroll {
  overflow: hidden;
}

body > * {
  flex-shrink: 0; /* sticky footer */
}

a {
  text-decoration: none;
}

#overlay {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 101;
}

#overlay.active {
  display: block;
}

.error {
  color: red;
}

input,
textarea,
button {
  font-family: inherit;
  padding: 0;
}

button {
  border: none;
  outline: none;
}

label {
  cursor: pointer;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

fieldset {
  border: none;
  margin: 0;
  padding: 0;
}

legend {
  display: block;
  margin: 0;
  padding: 0;
  border: none;
}

/* Grid styles */

.container {
  max-width: 1230px;
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px;
}

/* Utility */

.none {
  display: none !important;
}

/* Sticky Footer */

.footer {
  margin-top: auto; /* sticky footer */
}
